.TodoSearch {
    background: #F9FBFC;
    border-radius: 1px;
    border: 1px solid #000000;
    margin: 0 24px;
    height: 50px;
    width: calc(100% - 142px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    border-radius: 25px;
  }
  
  .TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 400;
  } 
 
  .TodoSearch:focus {
    outline-color: #00c8ff;
  }